.scoreBoard{

  position: relative;
  margin: 30px auto;
  text-align: center;
}

.gameArena{
  position: relative;
  margin-top:0;
  margin-right:auto;
  margin-bottom:0;
  margin-left:auto;
  padding:0px;
  width: 500px;
  height: 500px;
  border: 1px solid black;
  background-color: rgb(216, 213, 203);


}

.snake-dot{
  position: absolute;
  width: 2%;
  height: 2%;
  background: black;
  border: 1px solid yellow;
  z-index: 2;
  border-radius: 5px;
}
.snake-head{
  position: absolute;
  width: 2%;
  height: 2%;
  background-image: url("pacman-black.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 100%;
  z-index: 3;
  padding: 1px;
}

.snake-head.DOWN{
  transform: rotate(90deg);
}
.snake-head.LEFT{
  transform: rotate(180deg) scaleY(-1);;
}
.snake-head.UP{
  transform: rotate(270deg);
}
.food-dot{
  position: absolute;
  width: 3%;
  height: 3%;
  background-image: url("apple.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 100% 100%;

}